
@import '../styles/_styleVariables.css';
header{
  text-align: center;
}
.passageTitle{
  margin: 0;
  font-size: 3rem;
  border: none;
}
.passageMetadata{
  display: inline-block;
  margin: 0;
  font-size: 1rem;
}
h1 + .passageMetadata{
  margin-right: 2em;
}
article{
  font-size: 1.2rem;
  margin-bottom: 2em;
}
h1{
  font-size: 2.5rem;
  border-bottom: var(--borderColor) 1px solid;
  margin: 0;
  margin-bottom: .5em;
}
h2{
  font-size: 1.8rem;
  margin: 0;
}
h3{
  font-size: 1.3rem;
  margin: 0;
  margin-bottom: .5em;
}
h4{
  font-size: 1rem;
  margin: 0;
}
article > p{
  text-indent: 2em;
}
ol{
  list-style-position: inside;
  padding: 0;
  margin-left: 2em;
  font-size: 1.2rem;
}
ol pre{
  margin-left: 0;
}
pre{
  margin-left: 2em;
  background-color: #222;
  color: white;
  padding: 1em;
  line-height: 1.5em;
  border-radius: 4px;
  font-size: 1.2rem;
}
blockquote{
  background: #f3f3f3;
  padding: 1em;
  border-left: .4em #555 solid;
}
blockquote p{
  margin: 0;
}
.last{
  margin-right: 2em;
}



